
import { defineComponent } from "vue";
import BaseIcon from "./BaseIcon.vue";
import enumProp from "../helpers/enum-prop";
import { baseAlertEvents, baseAlertPropsType } from "./BaseAlert.types";

export default defineComponent({
  name: "BaseAlert",
  components: {
    BaseIcon,
  },
  props: {
    eventDate: {
      type: Date,
    },
    message: {
      type: String,
      default: "",
    },
    title: {
      type: String,
      required: true,
    },
    type: {
      ...enumProp("success", baseAlertPropsType),
      default: "success",
    },
  },
  emits: [...baseAlertEvents],
  data() {
    return {
      isClosed: !this.eventDate,
    };
  },
  computed: {
    classes(): string {
      const c = ["base-alert", `base-alert--${this.type}`];

      if (this.isClosed) {
        c.push("base-alert--closed");
      }

      return c.join(" ");
    },
    iconName(): string {
      switch (this.type) {
        case "information":
          return "info";
        case "success":
          return "checkmark";
        default:
          return this.type;
      }
    },
  },
  methods: {
    onClose() {
      this.isClosed = true;
      this.$emit("close");
    },
  },
  watch: {
    eventDate() {
      this.isClosed = false;
      this.$emit("open");
    },
  },
});
